import React, { useState, useEffect } from "react";
import Box from "@material-ui/core/Box";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import { Button } from "@material-ui/core";
import AddTask from "./AddTask/AddTask";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import useStyles from "./activityStyles";
import Copyright from "../../../UI/Copyrights";
import { useParams } from "react-router";
import { useStoreState } from "easy-peasy";
import TaskList from "./TaskList/TaskList";

const Activity = () => {
  const classes = useStyles();
  let { activityid } = useParams();

  let tasks = useStoreState(state => state.activity.tasks);

  let { type } = useStoreState(state => state.profile);
  let [tasksBuffer, setTasksBuffer] = useState([]);
  let user_id = useStoreState(state => state.profile.user_id);
  let alter_user_id = useStoreState(state => state.auth.profile.user_id);

  let isTasksLoading = useStoreState(state => state.activity.isFetching);
  let [date, setDate] = useState(new Date());
  let [name, setName] = useState([]);
  const [openModal, setOpenModal] = useState(false);

  useEffect(() => {
    if (!isTasksLoading) {
      setTasksBuffer(tasks);
    }
  }, [isTasksLoading]);

  const handleModalOpen = () => {
    setOpenModal(true);
  };

  const handleModalClose = () => {
    setOpenModal(false);
  };

  function filterByNames(tasks, names, isTransaction) {
    // side effect!! be careful in these cases
    let updatedTasks = names.length
      ? tasks.filter(task =>
          names.some(name => task.fname + " " + task.lname === name)
        )
      : tasks;
    setTasksBuffer(updatedTasks);
    if (isTransaction) filterByNames(updatedTasks, date, false);
  }

  function isTodaysTaskUpdated(tasks, user_id) {
    return tasks.some(task => {
      console.log(
        new Date(task.date).toLocaleDateString(),
        new Date().toLocaleDateString()
      );
      return (
        new Date(task.date).toLocaleDateString() ===
          new Date().toLocaleDateString() && user_id === task.user_id
      );
    });
  }

  function amIInThisActivity() {}

  function getAllUtil(tasks, field) {
    let uniqueTasks = [];
    tasks = tasks.map(task => ({
      name: task.fname + " " + task.lname
    }));
    tasks.forEach(task => {
      if (!uniqueTasks.some(uniqueTask => uniqueTask[field] === task[field])) {
        uniqueTasks.push({ [field]: task[field] });
      }
    });

    return uniqueTasks;
  }

  function filterByDate(tasks, date, isTransaction) {
    // side effect!! be careful in these cases
    let updatedTasks = date.length
      ? tasks.filter(
          task =>
            new Date(task.date).toLocaleDateString() ===
            new Date(date).toLocaleDateString()
        )
      : tasks;
    setTasksBuffer(updatedTasks);
    if (isTransaction) filterByNames(updatedTasks, name, false);
  }

  return (
    <main className={classes.content}>
      <div className={classes.appBarSpacer} />
      <Container className={classes.container}>
        <Grid container>
          <Grid container item xs={12} spacing={2}>
            <Grid item xs={12}>
              {!isTodaysTaskUpdated(tasks, user_id || alter_user_id) &&
              type !== "A" ? (
                <Button
                  href="#text-buttons"
                  color="primary"
                  onClick={handleModalOpen}
                  className={classes.spaceDown1}
                >
                  Add new task
                </Button>
              ) : null}
            </Grid>
            {!isTasksLoading ? (
              <>
                <Grid item xs={12} md={6} lg={3}>
                  <Autocomplete
                    className={classes.spaceDown1}
                    multiple
                    id="name-filter"
                    options={getAllUtil(tasks, "name")}
                    getOptionLabel={option => option.name}
                    filterSelectedOptions
                    onChange={(event, values) => {
                      console.log(values);
                      setName(values.map(value => value.name));
                      filterByNames(
                        tasks,
                        values.map(value => value.name),
                        true
                      );
                    }}
                    renderInput={params => (
                      <TextField
                        {...params}
                        variant="outlined"
                        label="Search by name"
                        placeholder="Enter the member's name"
                        fullWidth
                      />
                    )}
                  />
                </Grid>
                <Grid item xs={12} md={6} lg={3}>
                  <TextField
                    id="date"
                    label="Search by date"
                    type="date"
                    onChange={event => {
                      setDate(new Date(event.currentTarget.value));
                      filterByDate(tasks, event.currentTarget.value, true);
                    }}
                    className={classes.textField}
                    InputLabelProps={{
                      shrink: true
                    }}
                  />
                </Grid>
              </>
            ) : null}
          </Grid>

          <AddTask
            open={openModal}
            user_id={user_id || alter_user_id}
            activityId={activityid}
            handleClose={handleModalClose}
          />
          <TaskList
            activityId={activityid}
            tasks={tasksBuffer}
            user_id={user_id}
          />
          <Grid item xs={7} md={10} lg={12}>
            <Box pt={4}>
              <Copyright />
            </Box>
          </Grid>
        </Grid>
      </Container>
    </main>
  );
};

export default Activity;
