import React from "react";

import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import SettingsIcon from "@material-ui/icons/Settings";
import DashboardIcon from "@material-ui/icons/Dashboard";
import LogoutIcon from "@material-ui/icons/ExitToApp";
import PersonIcon from "@material-ui/icons/Person";

import { Link } from "react-router-dom";
import UploadCSV from "../../UploadCSV/UploadCSV";
import { useState } from "react";
import { Button } from "@material-ui/core";
import { useStoreActions } from "easy-peasy";

const DrawableItems = () => {
  let [isUploadModalOpen, setUploadModalOpen] = useState(false);

  let { type } = useStoreActions(state => state.profile);
  function handleModalOpen() {
    setUploadModalOpen(true);
  }
  function handleModalClose() {
    setUploadModalOpen(false);
  }
  return (
    <div>
      <ListItem button>
        <ListItemIcon>
          <DashboardIcon />
        </ListItemIcon>
        <Link to="/activities/dashboard">
          <ListItemText primary="Dashboard" />
        </Link>
      </ListItem>
      <ListItem button>
        <ListItemIcon>
          <LogoutIcon />
        </ListItemIcon>
        <Link to="/logout">
          <ListItemText primary="Logout" />
        </Link>
      </ListItem>
      <ListItem button>
        <ListItemIcon>
          <SettingsIcon />
        </ListItemIcon>
        <Link to="/activities/settings">
          <ListItemText primary="Settings" />
        </Link>
      </ListItem>
      <ListItem>
        <ListItemIcon>
          <PersonIcon />
        </ListItemIcon>
        <Link to="/activities/profile">
          <ListItemText primary="Profile" />
        </Link>
      </ListItem>
      <ListItem>
        {type === "A" ? (
          <Button
            href="#text-buttons"
            color="primary"
            onClick={handleModalOpen}
          >
            Upload CSV
          </Button>
        ) : null}
      </ListItem>
      <UploadCSV open={isUploadModalOpen} handleClose={handleModalClose} />
    </div>
  );
};

export default DrawableItems;
