import React, { useState, useEffect } from "react";

import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import AddCircle from "@material-ui/icons/AddCircle";
import { Paper, makeStyles } from "@material-ui/core";
import { useStoreState, useStoreActions } from "easy-peasy";
import LoadingContainer from "../../../UI/LoadingContainer";
import AddQualification from "./AddQualification";
import AddSkills from "./AddSkills";
// import useStyles from "./myInfoStyles";

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: "100vh",
    overflow: "auto"
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  },
  paper: {
    padding: theme.spacing(2),
    display: "flex",
    overflow: "auto",
    flexDirection: "column"
  },
  fixedHeight: {
    height: 240
  },
  fixedWidth: {
    maxWidth: "70vw",
    overflow: "auto"
  },
  paperStyling: {
    padding: 20
  },
  formElementWidth: {
    width: 250
  },
  formFullWidth: {
    width: "100%"
  }
}));

const MyInfo = () => {
  let [openQualificationModal, setOpenQualificationModal] = useState(false);
  let [openSkillsModal, setOpenSkillsModal] = useState(false);
  let {
    fname,
    lname,
    city,
    email,
    address_line1,
    address_line2,
    dist,
    state,
    branch,
    aboutme,
    phone,
    pin,
    skills,
    qualification
  } = useStoreState(state => state.profileMod.profile);

  let getprofile = useStoreActions(
    action => action.profileMod.FETCH_PROFILE_START
  );
  let isProfileFetching = useStoreState(state => state.profileMod.isFetching);

  function handleQualificationOpen() {
    setOpenQualificationModal(true);
  }

  function handleQualificationClose() {
    setOpenQualificationModal(false);
  }

  function handleSkillsOpen() {
    setOpenSkillsModal(true);
  }

  function handleSkillsClose() {
    setOpenSkillsModal(false);
  }

  let classes = useStyles();
  useEffect(() => {
    if (isProfileFetching) getprofile();
  }, [isProfileFetching]);
  return (
    <main className={classes.content}>
      <div className={classes.appBarSpacer} />

      <Container maxWidth="lg" className={classes.container}>
        <LoadingContainer isLoading={isProfileFetching}>
          <Paper className={classes.paperStyling}>
            <Grid container spacing={3}>
              <Grid item xs={12} lg={6}>
                <h2>{fname + " " + lname}</h2>
                <br />
                <h3>Address</h3>
                <p>{address_line1}</p>
                <p>{address_line2}</p>
                <p>
                  {city}, {dist}, {state}
                </p>
                <p>{pin}</p>
              </Grid>
              <Grid item xs={12} lg={6}>
                <br />
                <h3>Contact info</h3>
                <p>Phone : {phone}</p>
                <p>Email : {email}</p>
              </Grid>
              <Grid item lg={12}>
                <h3>About me</h3>
                <p>{aboutme}</p>
              </Grid>
              <Grid item xs={12} lg={6}>
                <h3>
                  Educational qualifications{" "}
                  <AddCircle
                    style={{ fontSize: 16, cursor: "pointer" }}
                    onClick={event => {
                      handleQualificationOpen();
                    }}
                  />
                  <AddQualification
                    handleClose={handleQualificationClose}
                    open={openQualificationModal}
                  />
                </h3>
                {qualification && qualification.length ? (
                  qualification.map(
                    ({ qualification, college, grade, board }) => (
                      <div key={qualification}>
                        <br />
                        <h3>{qualification}</h3>
                        <p>{college}</p>
                        <p>{grade}</p>
                        <p>{board}</p>
                      </div>
                    )
                  )
                ) : (
                  <p>Not available</p>
                )}
              </Grid>
              <Grid item xs={12} lg={6}>
                <h3>
                  Skillset{" "}
                  <AddCircle
                    style={{ fontSize: 16, cursor: "pointer" }}
                    onClick={event => {
                      handleSkillsOpen();
                    }}
                  />
                  <AddSkills
                    open={openSkillsModal}
                    handleClose={handleSkillsClose}
                  />
                </h3>
                {skills && skills.length ? (
                  skills.map(({ skills, title }) => (
                    <div key={skills}>
                      <br />
                      <h3>{skills}</h3>
                      <p>{title}</p>
                    </div>
                  ))
                ) : (
                  <p>Not available</p>
                )}
              </Grid>
              <Grid item lg={12}>
                <div style={{ width: "65vw" }}></div>
              </Grid>
            </Grid>
          </Paper>
        </LoadingContainer>
      </Container>
    </main>
  );
};

export default MyInfo;
