import React from "react";
import { FormControl, FormLabel, Input } from "@material-ui/core";
import { useState } from "react";
import Button from "@material-ui/core/Button";
import { TextValidator, ValidatorForm } from "react-material-ui-form-validator";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText
} from "@material-ui/core";

function UploadCSV({ open, handleClose }) {
  let [uploadFile, setUploadFile] = useState({});
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Upload Calender CSV</DialogTitle>
      <DialogContent>
        <DialogContentText>Please enter the below detail.</DialogContentText>
        <FormControl>
          <FormLabel>Upload the CSV file</FormLabel>
          <Input
            type="file"
            onChange={event => {
              console.log(event.target.files);
            }}
          ></Input>
        </FormControl>
      </DialogContent>
      <DialogActions>
        <Button onClick={event => handleClose()} color="primary">
          Cancel
        </Button>
        <Button
          onClick={e => {
            handleClose();
          }}
          color="primary"
        >
          Submit CSV
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default UploadCSV;
