import React from "react";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";

import Title from "../../../UI/Title";
import ActivityItem from "./ActivityListItem/ActivityListItem";
import ActivityHead from "./ActivityHead/ActivityHead";
import { useEffect } from "react";
import { useStoreActions, useStoreState } from "easy-peasy";
import LoadingContainer from "../../../UI/LoadingContainer";

export default function ActivityList() {
  let { type } = useStoreState(state => state.profile);
  let fetchActivities = useStoreActions(
    action => action.activities.FETCH_ACTIVITIES_START
  );
  let fetchActivitiesReset = useStoreActions(
    action => action.activities.FETCH_ACTIVITIES_REQUEST
  );
  let isActivitiesLoading = useStoreState(state => state.activities.isFetching);
  let isAuth = useStoreState(state => state.auth.isAuth);

  let isProfileLoaded = useStoreState(state => state.profile.isLoading);

  let activities = useStoreState(state => state.activities.activities);
  useEffect(() => {
    console.log(type, isProfileLoaded);
    if (!isProfileLoaded && isActivitiesLoading && isAuth)
      fetchActivities({ type });
    return () => fetchActivitiesReset();
  }, [type, isProfileLoaded]);

  return (
    <React.Fragment>
      <Title>Current Activities</Title>
      <LoadingContainer isLoading={isActivitiesLoading}>
        <Table size="small">
          <ActivityHead />
          <TableBody>
            {activities.map(row => (
              <ActivityItem row={row} key={row.activityid} />
            ))}
          </TableBody>
        </Table>
      </LoadingContainer>
    </React.Fragment>
  );
}
