import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import { Grid, Button } from "@material-ui/core";
import { useStoreActions } from "easy-peasy";

const useStyles = makeStyles({
  dispFlex: {
    display: "flex",
    justifyContent: "space-between"
  },
  dispFlexStart: {
    display: "flex",
    justifyContent: "flex-start"
  },
  spaceDown1: {
    marginBottom: 16
  },
  root: {
    minWidth: 275,
    margin: 8
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)"
  },
  title: {
    fontSize: 14,
    color: "#2d5dc4",
    fontWeight: "bold"
  },
  pos: {
    marginBottom: 12
  }
});
function TaskItem({ row, user_id, activity_id }) {
  let deleteTask = useStoreActions(action => action.activity.DELETE_TASK_START);
  const classes = useStyles();
  function iWasAssigned(task, user_id) {
    return task.user_id === user_id;
  }
  function iAmSupervisor(task, user_id) {
    return task.assigned_by === user_id;
  }
  return (
    <Grid item xs={12} md={6} lg={3}>
      <Card className={classes.root}>
        <CardContent>
          <div className={classes.dispFlex}>
            <Typography
              className={classes.title}
              color="textSecondary"
              gutterBottom
            >
              {new Date(row.date).toLocaleDateString()}
            </Typography>

            <Typography
              className={classes.title}
              color="textSecondary"
              gutterBottom
            >
              {"Progress " + (row.progress || "NA")}
            </Typography>
          </div>
          <Typography
            variant="h5"
            component="h2"
            className={classes.spaceDown1}
          >
            {row.task_description}
          </Typography>
          <Typography variant="body1" component="p">
            Assigned to : {row.fname + " " + row.lname}
          </Typography>
          <Typography
            variant="body1"
            component="p"
            className={classes.spaceDown1}
          >
            Status : {row.status_eod || "Not entered"}
            <br />
            Knowledge gained : {row.knowledge_gained || "Not entered"}
          </Typography>
          <div className={classes.dispFlexStart}>
            {/* {iAmSupervisor(row, user_id) ? (
              <Button color="primary">Comment</Button>
            ) : null} */}

            {iWasAssigned(row, user_id) ? (
              <Button
                onClick={e => {
                  deleteTask({
                    report_id: row.reportid,
                    date: row.date,
                    activity_id: activity_id
                  });
                }}
                size="small"
                color="secondary"
              >
                Delete
              </Button>
            ) : null}
          </div>
        </CardContent>
      </Card>
    </Grid>
  );
}

export default TaskItem;
