import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles, FormControl } from "@material-ui/core";
import { TextValidator, ValidatorForm } from "react-material-ui-form-validator";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
  InputLabel,
  Select,
  MenuItem
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import { useStoreState, useStoreActions } from "easy-peasy";

const useStyles = makeStyles(theme => ({
  formControl: {
    minWidth: 200
  },
  selectEmpty: {
    marginTop: theme.spacing(2)
  }
}));

const AddActivity = ({ open, handleClose }) => {
  const classes = useStyles();
  const activityTypes = ["Project", "Training", "Workshop"];
  let users = useStoreState(state => state.auth.users);
  const [domain, setDomain] = React.useState("");
  const [selectedTeam, setTeam] = useState([]);
  const [supervisor, setSuperVisor] = useState({});
  const [activityTitle, setTitle] = useState("");
  const [activity_type, setType] = useState(activityTypes[0]);

  const postActivity = useStoreActions(
    action => action.activities.POST_ACTIVITY_START
  );
  const handleChange = event => {
    setDomain(event.target.value);
  };

  const handleTypeChange = event => {
    setType(event.target.value);
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Add Activity</DialogTitle>
      <DialogContent>
        <DialogContentText>Please enter the below detail.</DialogContentText>
        <TextField
          autoFocus
          margin="dense"
          value={activityTitle}
          onChange={e => setTitle(e.target.value)}
          id="activity_title"
          label="Activity title"
          type="text"
          fullWidth
        />
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-simple-select-label">Activity domain</InputLabel>
          <Select
            required
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={domain}
            onChange={handleChange}
          >
            <MenuItem value={"web development"}>Web development</MenuItem>
            <MenuItem value={"IOT"}>IOT</MenuItem>
            <MenuItem value={"android development"}>Android</MenuItem>
            <MenuItem value={"Artificial Intelligence"}>AI</MenuItem>
          </Select>
        </FormControl>
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-simple-select-label">Activity domain</InputLabel>
          <Select
            required
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={activity_type}
            onChange={handleTypeChange}
          >
            <MenuItem value={"Project"}>Project</MenuItem>
            <MenuItem value={"Workshop"}>Workshop</MenuItem>
            <MenuItem value={"Training"}>Training</MenuItem>
          </Select>
        </FormControl>
        <Autocomplete
          id="tags-standard"
          multiple
          options={users.filter(
            member =>
              !selectedTeam.some(
                selectedMember => selectedMember.user_id === member.user_id
              )
          )}
          getOptionLabel={option => option.fname + " " + option.lname}
          onChange={(e, value) => {
            setTeam(value);
            console.log(value);
          }}
          renderInput={params => (
            <TextField
              required
              {...params}
              variant="standard"
              label="Team members"
              placeholder="Team"
              fullWidth
            />
          )}
        />
        <Autocomplete
          id="tags-standard"
          options={selectedTeam}
          getOptionLabel={option => option.fname + " " + option.lname}
          onChange={(e, value) => {
            setSuperVisor(value);
            console.log(value);
          }}
          renderInput={params => (
            <TextField
              required
              {...params}
              variant="standard"
              label="Supervisor"
              placeholder="Enter the supervisor"
              fullWidth
            />
          )}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button
          onClick={e => {
            console.log({
              work: domain,
              user_id: selectedTeam.map(member => member.user_id),
              super_id: supervisor.user_id,
              activity_type,
              title: activityTitle
            });
            postActivity({
              work: domain,
              user_id: selectedTeam.map(member => member.user_id),
              super_id: supervisor.user_id,
              activity_type,
              title: activityTitle
            });
            handleClose();
          }}
          color="primary"
        >
          Submit activity
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddActivity;
