import React, { useEffect } from "react";
import DrawableNav from "../DrawableNav/DrawableNav";
import CssBaseline from "@material-ui/core/CssBaseline";
import Dashboard from "./Dashboard";

import { useStoreState, useStoreActions } from "easy-peasy";
import useStyles from "./dashboardStyles";
import TopNav from "../TopNav/TopNav";
import { Switch, Route } from "react-router";
import Activity from "./Activity/Activity";
import SettingsDemo from "../Profile/Settings/Settings";
import MyInfo from "../Profile/MyInfo/MyInfo";

const ActivitiesDemo = () => {
  let isCalenderLoading = useStoreState(state => state.attendence.isFetching);
  let fetchAttendance = useStoreActions(
    action => action.attendence.FETCH_ATTENDENCE_START
  );

  useEffect(() => {
    if (isCalenderLoading) fetchAttendance();
  }, []);

  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>
      <CssBaseline />
      <TopNav open={open} handleDrawerOpen={handleDrawerOpen} />
      <DrawableNav handleDrawerClose={handleDrawerClose} open={open} />
      <Switch>
        <Route path="/activities/dashboard">
          <Dashboard />
        </Route>
        <Route path="/activities/activity/:activityid">
          <Activity />
        </Route>
        <Route path="/activities/settings">
          <SettingsDemo />
        </Route>

        <Route path="/activities/profile">
          <MyInfo />
        </Route>
      </Switch>
    </div>
  );
};

export default ActivitiesDemo;
