import React from "react";

import Title from "../../../../UI/Title";
import TaskItem from "./TaskItem/TaskItem";
import { useEffect } from "react";
import { useStoreActions, useStoreState } from "easy-peasy";
import LoadingContainer from "../../../../UI/LoadingContainer";
import { Grid } from "@material-ui/core";

function TaskList({ activityId, tasks, user_id }) {
  let isTasksLoading = useStoreState(state => state.activity.isFetching);
  let fetchTasks = useStoreActions(action => action.activity.FETCH_TASKS_START);
  let clearTasks = useStoreActions(action => action.activity.CLEAR_TASKS);

  useEffect(() => {
    if (isTasksLoading) fetchTasks({ activityId });
    return () => clearTasks();
  }, []);

  return (
    <React.Fragment>
      <Grid item xs={12}>
        <Title>Associated tasks</Title>
      </Grid>
      <LoadingContainer isLoading={isTasksLoading}>
        {tasks.length ? (
          tasks.map(row => (
            <TaskItem
              activity_id={activityId}
              user_id={user_id}
              row={row}
              key={row}
            />
          ))
        ) : (
          <p>No tasks available under this activity</p>
        )}
      </LoadingContainer>
    </React.Fragment>
  );
}

export default TaskList;
