import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles, FormControl } from "@material-ui/core";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText
} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import { useStoreActions, useStoreState } from "easy-peasy";

const useStyles = makeStyles(theme => ({
  dateBox: {
    margin: 10
  }
}));
const AddLeaveNote = ({ open, handleClose }) => {
  const [reason, setReason] = useState("");
  const [StartDate, setStartDate] = useState("");
  const [EndDate, setEndDate] = useState("");
  const [adminmail, setAdminmail] = useState("");
  const type = useStoreState(state => state.profile.type);
  let user_id = useStoreState(state => state.profile.user_id);
  let alter_user_id = useStoreState(state => state.auth.profile.user_id);

  const postLeaveNote = useStoreActions(
    action => action.attendence.POST_LEAVE_NOTE_START
  );
  const classes = useStyles();
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Make a new leave note</DialogTitle>
      <DialogContent>
        <DialogContentText>Please enter the below detail.</DialogContentText>
        <TextField
          autoFocus
          margin="dense"
          id="leave_reason"
          value={reason}
          onChange={e => setReason(e.target.value)}
          label="Reason for the leave"
          type="text"
          fullWidth
        />
        <FormControl className={classes.dateBox}>
          <TextField
            id="date"
            label="Start date"
            type="date"
            value={StartDate}
            onChange={e => setStartDate(e.target.value)}
            InputLabelProps={{
              shrink: true
            }}
          />
        </FormControl>
        <FormControl className={classes.dateBox}>
          <TextField
            id="date"
            label="End date"
            value={EndDate}
            onChange={e => setEndDate(e.target.value)}
            type="date"
            InputLabelProps={{
              shrink: true
            }}
          />
        </FormControl>
        <FormControl className={classes.dateBox}>
          <TextField
            id="adminmail"
            label="Admin mail"
            value={adminmail}
            onChange={e => setAdminmail(e.target.value)}
          />
        </FormControl>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button
          onClick={e => {
            handleClose();
            let user_id_final = user_id || alter_user_id;
            postLeaveNote({
              reason,
              StartDate,
              EndDate,
              adminmail,
              type,
              user_id: user_id_final
            });
          }}
          color="primary"
        >
          Submit
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddLeaveNote;
