import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText
} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import { useStoreActions } from "easy-peasy";

const useStyles = makeStyles(theme => ({
  formControl: {
    minWidth: 200
  },
  selectEmpty: {
    marginTop: theme.spacing(2)
  }
}));

const AddQualification = ({ open, handleClose }) => {
  const [qualification, setQualification] = React.useState("");
  const [college, setCollege] = React.useState("");
  const [grade, setGrade] = React.useState(0);
  const [board, setBoard] = React.useState("");
  const postQualification = useStoreActions(
    action => action.profileMod.POST_QUALIFICATION_START
  );
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Add a new qualification</DialogTitle>
      <DialogContent>
        <DialogContentText>Please enter the below detail.</DialogContentText>
        <TextField
          autoFocus
          margin="dense"
          value={qualification}
          onChange={e => setQualification(e.target.value)}
          id="qualification"
          label="Qualification"
          type="text"
          fullWidth
        />
        <TextField
          autoFocus
          margin="dense"
          value={grade}
          onChange={e => {
            console.log(isNaN(e.target.value));
            if (!isNaN(e.target.value) && 0 >= e.target.value <= 10)
              setGrade(e.target.value);
          }}
          id="qualification_grade"
          label="Grade"
          type="number"
          inputProps={{ max: 10, min: 0 }}
          fullWidth
        />
        <TextField
          autoFocus
          margin="dense"
          value={college}
          onChange={e => setCollege(e.target.value)}
          id="qualification_college"
          label="College"
          type="text"
          fullWidth
        />
        <TextField
          autoFocus
          margin="dense"
          value={board}
          onChange={e => setBoard(e.target.value)}
          id="qualification_board"
          label="Board"
          type="text"
          fullWidth
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button
          onClick={e => {
            console.log({
              board,
              college,
              qualification,
              grade
            });
            postQualification({
              board,
              college,
              qualification,
              grade
            });
            handleClose();
          }}
          color="primary"
        >
          Submit activity
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddQualification;
