import React, { useState, useEffect } from "react";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import { Button, Paper, FormControl, TextField } from "@material-ui/core";
import { useStoreState, useStoreActions } from "easy-peasy";
import { makeStyles } from "@material-ui/core/styles";
import Autocomplete from "@material-ui/lab/Autocomplete";

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: "100vh",
    overflow: "auto"
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  },
  paper: {
    padding: theme.spacing(2),
    display: "flex",
    overflow: "auto",
    flexDirection: "column"
  },
  fixedHeight: {
    height: 240
  },
  fixedWidth: {
    maxWidth: "70vw",
    overflow: "auto"
  },
  paperStyling: {
    padding: 20
  },
  formElementWidth: {
    width: 250
  },
  formFullWidth: {
    width: "100%"
  }
}));

// don't worry about the size, it's just number of form fields
const SettingsDemo = () => {
  let prevEmail = useStoreState(state => state.profileMod.profile.email);
  let prevFname = useStoreState(state => state.profileMod.profile.fname);
  let prevLname = useStoreState(state => state.profileMod.profile.lname);
  let prevSex = useStoreState(state => state.profileMod.profile.sex);
  let prevSem = useStoreState(state => state.profileMod.profile.sem);
  let prevDob = useStoreState(state => state.profileMod.profile.dob);
  let prevPhone = useStoreState(state => state.profileMod.profile.phone);
  let prevAddress1 = useStoreState(
    state => state.profileMod.profile.address_line1
  );
  let prevAddress2 = useStoreState(
    state => state.profileMod.profile.address_line2
  );
  let prevCity = useStoreState(state => state.profileMod.profile.city);
  let prevDist = useStoreState(state => state.profileMod.profile.dist);
  let prevState = useStoreState(state => state.profileMod.profile.state);
  let prevPin = useStoreState(state => state.profileMod.profile.pin);
  let prevaboutme = useStoreState(state => state.profileMod.profile.aboutme);
  let prevbranch = useStoreState(state => state.profileMod.profile.branch);

  let updateProfile = useStoreActions(
    action => action.profileMod.POST_PROFILE_START
  );

  let [sex, setSex] = useState(prevSex || "Male");
  let [sem, setSem] = useState("" + prevSem || "First1");
  let [branch, setbranch] = useState(prevbranch || "Computer Science");
  let [fname, setFname] = useState(prevFname || "");
  let [lname, setLname] = useState(prevLname || "");
  let [dob, setDob] = useState(new Date(prevDob) || new Date("2000-01-01"));
  let [email, setEmail] = useState(prevEmail || "");
  let [phone, setPhone] = useState(prevPhone || "");
  let [address_line1, setaddress_line1] = useState(prevAddress1 || "");
  let [address_line2, setaddress_line2] = useState(prevAddress2 || "");
  let [city, setCity] = useState(prevCity || "");
  let [dist, setDist] = useState(prevDist || "");
  let [state, setSt] = useState(prevState || "");
  let [pin, setPin] = useState(prevPin || "");
  let [aboutme, setaboutme] = useState(prevaboutme || "");

  const classes = useStyles();

  return (
    <main className={classes.content}>
      <div className={classes.appBarSpacer} />

      <Container maxWidth="lg" className={classes.container}>
        <Paper className={classes.paperStyling}>
          <form>
            <Grid container spacing={3}>
              <Grid item md={6} lg={3}>
                <FormControl fullWidth={true}>
                  <TextField
                    className={classes.formElementWidth}
                    label="Enter your name"
                    id="profile-name"
                    value={fname}
                    onChange={e => {
                      setFname(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={6} lg={3}>
                <FormControl fullWidth={true}>
                  <TextField
                    className={classes.formElementWidth}
                    label="Enter your last name"
                    id="profile-name"
                    value={lname}
                    onChange={e => {
                      setLname(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={6} lg={3}>
                <FormControl fullWidth={true}>
                  <TextField
                    className={classes.formElementWidth}
                    label="Enter your email"
                    id="profile-email"
                    value={email}
                    type="email"
                    onChange={e => {
                      setEmail(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item xs={12} lg={3}>
                <FormControl>
                  <label>Date of birth</label>
                  <TextField
                    id="profile-date"
                    value={dob}
                    type="date"
                    onChange={e => {
                      setDob(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item xs={6} lg={3}>
                <FormControl>
                  <Autocomplete
                    className={classes.formElementWidth}
                    id="profile-sex"
                    defaultValue={sex}
                    options={["Male", "Female", "Other"]}
                    filterSelectedOptions
                    onChange={event => {
                      setSex(event.target.value);
                    }}
                    renderInput={params => (
                      <TextField
                        {...params}
                        variant="outlined"
                        label="Enter the gender"
                        placeholder="Enter the gender"
                        fullWidth
                      />
                    )}
                  />
                </FormControl>
              </Grid>
              <Grid item xs={12} md={3}>
                <FormControl>
                  <Autocomplete
                    className={classes.formElementWidth}
                    id="profile-branch"
                    defaultValue={branch}
                    options={[
                      "Computer science",
                      "Mechanical",
                      "Civil",
                      "Electrical and Electronics",
                      "Electronics and Communications"
                    ]}
                    filterSelectedOptions
                    onChange={event => {
                      console.log(event.target.lastChild.textContent);
                      setbranch(event.target.lastChild.textContent);
                    }}
                    renderInput={params => (
                      <TextField
                        {...params}
                        variant="outlined"
                        label="Enter the branch"
                        placeholder="Enter the branch"
                        fullWidth
                      />
                    )}
                  />
                </FormControl>
              </Grid>
              <Grid item md={6} lg={3}>
                <FormControl>
                  <TextField
                    label="Enter your semester"
                    InputProps={{ inputProps: { min: 1, max: 8 } }}
                    id="profile-sem"
                    value={sem}
                    type="number"
                    onChange={e => {
                      setSem(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item xs={3}>
                <FormControl fullWidth={true}>
                  <TextField
                    label="Enter your phone"
                    id="profile-phone"
                    value={phone}
                    type="phone"
                    onChange={e => {
                      setPhone(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>

              <Grid item xs={12}>
                <FormControl fullWidth={true}>
                  <TextField
                    label="Address line 1"
                    id="profile-address-1"
                    value={address_line1}
                    onChange={e => {
                      setaddress_line1(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={12}>
                <FormControl fullWidth={true}>
                  <TextField
                    label="Address line 2"
                    id="profile-address-2"
                    value={address_line2}
                    onChange={e => {
                      setaddress_line2(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={6} lg={3}>
                <FormControl>
                  <TextField
                    label="Enter your city"
                    id="profile-city"
                    value={city}
                    onChange={e => {
                      setCity(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={6} lg={3}>
                <FormControl>
                  <TextField
                    label="Enter your district"
                    id="profile-dist"
                    value={dist}
                    onChange={e => {
                      setDist(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={6} lg={3}>
                <FormControl>
                  <TextField
                    label="Enter your state"
                    id="profile-state"
                    value={state}
                    onChange={e => {
                      setSt(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={6} lg={3}>
                <FormControl>
                  <TextField
                    label="Enter your pin"
                    id="profile-pin"
                    value={pin}
                    onChange={e => {
                      setPin(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item md={12}>
                <FormControl>
                  <TextField
                    multiline
                    label="About me"
                    id="profile-about"
                    value={aboutme}
                    onChange={e => {
                      setaboutme(e.target.value);
                    }}
                  />
                </FormControl>
              </Grid>
              <Grid item xs={12} md={6} lg={2}>
                <Button
                  href="#text-buttons"
                  color="primary"
                  onClick={e => {
                    updateProfile({
                      sex,
                      branch,
                      fname,
                      lname,
                      sem,
                      dob,
                      email,
                      phone,
                      address_line1,
                      address_line2,
                      city,
                      dist,
                      state,
                      pin,
                      aboutme
                    });
                  }}
                >
                  Submit
                </Button>
              </Grid>
            </Grid>
          </form>
        </Paper>
      </Container>
    </main>
  );
};

export default SettingsDemo;
