import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText
} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import { useStoreActions } from "easy-peasy";

const useStyles = makeStyles(theme => ({
  formControl: {
    minWidth: 200
  },
  selectEmpty: {
    marginTop: theme.spacing(2)
  }
}));

const AddSkills = ({ open, handleClose }) => {
  const [skills, setSkills] = React.useState("");
  const [title, setTitle] = React.useState("");
  const postSkills = useStoreActions(
    action => action.profileMod.POST_SKILLS_START
  );
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Add a new skill</DialogTitle>
      <DialogContent>
        <DialogContentText>Please enter the below detail.</DialogContentText>
        <TextField
          autoFocus
          margin="dense"
          value={skills}
          onChange={e => setSkills(e.target.value)}
          id="skills"
          label="Skill"
          type="text"
          fullWidth
        />
        <TextField
          autoFocus
          margin="dense"
          value={title}
          onChange={e => {
            setTitle(e.target.value);
          }}
          id="skills_title"
          label="Title"
          type="text"
          fullWidth
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button
          onClick={e => {
            postSkills({
              skills,
              title
            });
            handleClose();
          }}
          color="primary"
        >
          Submit activity
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddSkills;
