import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles, FormControl } from "@material-ui/core";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
  InputLabel,
  Select,
  MenuItem
} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import { useStoreActions } from "easy-peasy";

const useStyles = makeStyles(theme => ({
  formControl: {
    minWidth: 200
  },
  selectEmpty: {
    marginTop: theme.spacing(2)
  }
}));

const AddTask = ({ open, handleClose, user_id, activityId }) => {
  const classes = useStyles();
  let postTask = useStoreActions(action => action.activity.POST_TASK_START);
  const [task_description, setDescription] = useState();
  const [dependency, setDependency] = useState(0);
  const [category, setCategory] = useState("Web development");
  const [endStatus, setStatus] = useState("");
  const [progress, setProgress] = useState("");
  const [newskill, setNewskill] = useState("");
  const [knowledegeGained, setKnowledge] = useState("");

  const handleCategoryChange = event => {
    setCategory(event.target.value);
  };

  const handleSetStatus = event => {
    setStatus(event.target.value);
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Add Activity</DialogTitle>
      <DialogContent>
        <DialogContentText>Please enter the below detail.</DialogContentText>
        <TextField
          autoFocus
          margin="dense"
          id="task_description"
          label="Task description"
          type="text"
          fullWidth
          value={task_description}
          onChange={e => setDescription(e.target.value)}
        />
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-simple-select-label">
            Category of work
          </InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={category}
            onChange={handleCategoryChange}
          >
            <MenuItem value={"Web development"}>Web development</MenuItem>
            <MenuItem value={"AI"}>Aritificial Intelligence</MenuItem>
            <MenuItem value={"IOT"}>IOT</MenuItem>
          </Select>
        </FormControl>
        <TextField
          autoFocus
          margin="dense"
          id="task_dependency"
          label="Task dependency"
          type="number"
          fullWidth
          value={dependency}
          inputProps={{ min: "0", max: "100", step: "1" }}
          onChange={e => setDependency(e.target.value)}
        />
        <TextField
          autoFocus
          margin="dense"
          id="task_progress"
          label="Task progress"
          type="number"
          fullWidth
          inputProps={{ min: "0", max: "100", step: "1" }}
          value={progress}
          onChange={e => setProgress(e.target.value)}
        />
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-simple-select-label">Status</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={endStatus}
            onChange={handleSetStatus}
          >
            <MenuItem value={"Ongoing"}>Ongoing</MenuItem>
            <MenuItem value={"In progress"}>In progress</MenuItem>
            <MenuItem value={"Completed"}>Completed</MenuItem>
          </Select>
        </FormControl>
        <TextField
          autoFocus
          margin="dense"
          id="knowledge_gained"
          label="Knowledge Gained"
          type="text"
          fullWidth
          value={knowledegeGained}
          onChange={e => setKnowledge(e.target.value)}
        />
        <TextField
          autoFocus
          margin="dense"
          id="skills_gained"
          label="New skill gained"
          type="text"
          fullWidth
          value={newskill}
          onChange={e => setNewskill(e.target.value)}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button
          onClick={() => {
            const task = [{ user_id, task_description, dependency, category }];
            const report = {
              user_id: task,
              activityid: activityId,
              milestone: "",
              suid: user_id
            };
            const progressInfo = {
              userId: user_id,
              endStatus,
              progress,
              knowledegeGained,
              newSkills: newskill
            };
            postTask({ report, progressInfo, user_id });
            handleClose();
          }}
          color="primary"
        >
          Submit activity
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddTask;
