import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import CheckIcon from "@material-ui/icons/Check";
import CancelIcon from "@material-ui/icons/Cancel";

import { useEffect } from "react";
import { useStoreActions, useStoreState } from "easy-peasy";
import LoadingContainer from "../../../UI/LoadingContainer";
import { useState } from "react";
import {
  Button,
  Paper,
  ListItemSecondaryAction,
  ListItem
} from "@material-ui/core";

// to be seperated
const useStyles = makeStyles(theme => ({
  root: {
    zIndex: 3,
    backgroundColor: theme.palette.background.paper
  },
  listContainer: {
    padding: 5
  },
  list: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  },
  listItem: {
    padding: 10,
    marginBottom: 2
  },
  listIcon: {
    marginRight: 10
  }
}));

export default function LeaveNotes({ user_id }) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  let { type } = useStoreState(state => state.profile);
  let fetchLeaveNotes = useStoreActions(
    action => action.attendence.FETCH_LEAVE_NOTES_START
  );
  let fetchLeaveNotesReset = useStoreActions(
    action => action.attendence.FETCH_LEAVE_NOTES_REQUEST
  );
  let updateLeaveNotes = useStoreActions(
    action => action.attendence.UPDATE_LEAVE_NOTE_START
  );
  let isLeaveNotesLoading = useStoreState(
    state => state.attendence.isLeaveFetching
  );
  let leaveNotes = useStoreState(state => state.attendence.leaveNotes);

  useEffect(() => {
    if (isLeaveNotesLoading) fetchLeaveNotes({ user_id });
    return () => fetchLeaveNotesReset();
  }, []);

  return (
    <LoadingContainer isLoading={isLeaveNotesLoading}>
      <Button
        href="#text-buttons"
        color="primary"
        onClick={() => setOpen(!open)}
      >
        {!open ? "View" : "Hide"} leave notes
      </Button>
      <Collapse in={open}>
        <div className={classes.root}>
          <List className={classes.list}>
            <Paper>
              {leaveNotes.length ? (
                leaveNotes.map(item => (
                  <ListItem
                    dense
                    key={item._id}
                    style={{
                      backgroundColor:
                        item.status === "accepted"
                          ? "#a8ff9e"
                          : item.status === "rejected"
                          ? "#ff8279"
                          : "#fff"
                    }}
                  >
                    <ListItemText
                      className={classes.listItem}
                      primary={item.reason}
                      secondary={
                        new Date(item.StartDate).toLocaleDateString() +
                        " to " +
                        new Date(item.EndDate).toLocaleDateString()
                      }
                    />
                    {type === "A" ? (
                      <ListItemSecondaryAction>
                        <CheckIcon
                          className={classes.listIcon}
                          color="action"
                          onClick={event => {
                            let {
                              uuid,
                              CurrentDate,
                              adminmail,
                              EndDate,
                              StartDate
                            } = item;
                            updateLeaveNotes({
                              uuid,
                              CurrentDate,
                              adminmail,
                              EndDate,
                              StartDate,
                              status: "accepted"
                            });
                          }}
                        />
                        <CancelIcon
                          onClick={event => {
                            let {
                              uuid,
                              CurrentDate,
                              adminmail,
                              EndDate,
                              StartDate
                            } = item;
                            updateLeaveNotes({
                              uuid,
                              CurrentDate,
                              adminmail,
                              EndDate,
                              StartDate,
                              status: "rejected"
                            });
                          }}
                        />
                      </ListItemSecondaryAction>
                    ) : null}
                  </ListItem>
                ))
              ) : (
                <ListItemText
                  style={{ padding: "16px" }}
                  primary="No leave notes present"
                />
              )}
            </Paper>
          </List>
        </div>
      </Collapse>
    </LoadingContainer>
  );
}
